import React from 'react'
import './resources/Header.css'
import logo from './resources/logo.svg'

function Header(){
    return(
<header className="header-container">
    <div className="inner-header">
        <a href="/" className={"nav-logo"}>
            <img src={logo} alt="Logo"/>
            </a>
    <nav className="navbar-custom">
        <ul>
           <li><button className="nav-item selected selectable" href="/">Home</button></li> &nbsp;
           <li><button type="button" className="nav-item disabled" href="#" disabled>About</button></li> &nbsp;
           <li><button type="button" className="nav-item disabled" href="#" disabled>Blog</button></li>
        </ul>
    </nav>
    </div>
</header>
    )
}
export default Header